// #region catalog 
.catalog__cards {
  display: grid;
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  align-items: center;

  width: 100%;
  padding-top: 40px;
  column-gap: 48px;
  row-gap: 50px;

  @media (max-width: 1200px) {
    grid-template-columns: repeat(auto-fit, 200px);
  }
}
